
@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100;200;300;400;500;600;700;900&display=swap');

@mixin mobile {
  @media screen and (max-width: 480px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (max-width: 768px) {
    @content;
  }
}
@mixin laptop {
  @media screen and (max-width: 1200px) {
    @content;
  }
}

*{
    font-family: 'Libre Franklin', sans-serif;
}

.formContainer{
    background: rgb(30,96,190);
    background: linear-gradient(70deg, rgba(30,96,190,1) 19%, rgba(235,0,255,1) 98%);
    background-image: url('./imgs/wavygrassimg.png');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    
    
    .formWrapper{        
        backdrop-filter: blur(30px);
        background: rgba(227, 241, 227, 0.288);
        box-shadow: -5px -4px 8px rgba(255, 255, 255, 0.103);
        box-shadow: 4px 4px 8px rgba(59, 59, 59, 0.637);
        padding: 34px 48px;
        border-radius: 32px;

        display: flex;
        align-items: center;
        flex-direction: column;

        .logo{
            font-size: 1.5rem;
            font-weight: 500;
        }
        hr{
            width: 90%;
            height: 1px;
            background: rgb(122, 122, 122);
            border: none;
            margin: 10px 0;
        }
        .title{
            font-size: 1.2rem;
        }

        form{
            display: flex;
            flex-direction: column;
            gap: 20px;
            width: 340px;
            margin-top: 25px;
            
            
            input{
                font-size: 14px;
                padding: 8px 10px;
                border-radius: 10px;
                outline: none;
                border: none;
                box-shadow: 5px 4px 6px rgba(105, 105, 105, 0.493);
                background: rgb(255, 255, 255);
            }


            button{
                padding: 14px 0;
                font-weight: 500;
                border-radius: 14px;
                background: #8595C8;
                color: white;
                border: none;
                outline: none;
                cursor: pointer;
            }

            

            label{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 160px;
                gap: 10px;
                color: #eeeeee;
                font-size: 14px;
                margin: 0 auto;
                margin-bottom: 10px;
                cursor: pointer;
                img{
                    width: 40px;
                }
            }

        }

        .error{
            width: 50%;
            margin: 10px 0;
            text-align: center;
            font-size: 14px;
            padding: 10px 0;
            color: #000000;
            background: #f841416b;
            border-radius: 32px;
            backdrop-filter: blur(100px);
        }

        p{
            margin-top: 10px;
        }
    }
}


.home {
    background-color: #a7bcff;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  
    .container {
      border: 1px solid white;
      border-radius: 10px;
      width: 65%;
      height: 80%;
      display: flex;
      overflow: hidden;
      @include tablet {
       width: 90%;
      }
  
      .sidebar {
        flex: 1;
        background-color: #3e3c61;
        position: relative;
  
        .navbar {
          display: flex;
          align-items: center;
          background-color: #2f2d52;
          height: 50px;
          padding: 10px;
          justify-content: space-between;
          color: #ddddf7;
  
          .logo {
            font-weight: bold;
            @include tablet {
              display: none;
            }
          }
  
          .user {
            display: flex;
            gap: 10px;
  
            img {
              background-color: #ddddf7;
              height: 24px;
              width: 24px;
              border-radius: 50%;
              object-fit: cover;
            }
  
            button {
              background-color: #5d5b8d;
              color: #ddddf7;
              font-size: 10px;
              border: none;
              cursor: pointer;
              @include tablet {
                position: absolute;
                bottom: 10px;
              }
            }
          }
        }
        .search {
          border-bottom: 1px solid gray;
  
          .searchForm {
            padding: 10px;
  
            input {
              background-color: transparent;
              border: none;
              color: white;
              outline: none;
  
              &::placeholder {
                color: lightgray;
              }
            }
          }
        }
  
        .userChat {
          padding: 10px;
          display: flex;
          align-items: center;
          gap: 10px;
          color: white;
          cursor: pointer;
  
          &:hover {
            background-color: #2f2d52;
          }
  
          img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            object-fit: cover;
          }
  
          .userChatInfo {
            span {
              font-size: 18px;
              font-weight: 500;
            }
            p {
              font-size: 14px;
              color: lightgray;
            }
          }
        }
      }
      .chat {
        flex: 2;
  
        .chatInfo {
          height: 50px;
          background-color: #5d5b8d;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 10px;
          color: lightgray;
        }
  
        .chatIcons {
          display: flex;
          gap: 10px;
  
          img {
            height: 24px;
            cursor: pointer;
          }
        }
  
        .messages {
          background-color: #ddddf7;
          padding: 10px;
          height: calc(100% - 160px);
          overflow: scroll;
  
          .message {
            display: flex;
            gap: 20px;
            margin-bottom: 20px;
  
            .messageInfo {
              display: flex;
              flex-direction: column;
              color: gray;
              font-weight: 300;
  
              img {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;
              }
            }
            .messageContent {
              max-width: 80%;
              display: flex;
              flex-direction: column;
              gap: 10px;
  
              p {
                background-color: white;
                padding: 10px 20px;
                border-radius: 0px 10px 10px 10px;
                max-width: max-content;
              }
  
              img {
                width: 50%;
              }
            }
  
            &.owner {
              flex-direction: row-reverse;
  
              .messageContent {
                align-items: flex-end;
                p {
                  background-color: #8da4f1;
                  color: white;
                  border-radius: 10px 0px 10px 10px;
                }
              }
            }
          }
        }
  
        .input {
          height: 50px;
          background-color: white;
          padding: 10px;
          display: flex;
          align-items: center;
          justify-content: space-between;
  
          input {
            width: 100%;
            border: none;
            outline: none;
            color: #2f2d52;
            font-size: 18px;
  
            &::placeholder {
              color: lightgray;
            }
          }
  
          .send {
            display: flex;
            align-items: center;
            gap: 10px;
  
            img {
              height: 24px;
              cursor: pointer;
            }
  
            button {
              border: none;
              padding: 10px 15px;
              color: white;
              background-color: #8da4f1;
              cursor: pointer;
            }
          }
        }
      }
    }
}

//google login button

$white: #fff;
$google-blue: #4285f4;
$button-active-blue: #1669F2;

.btnGoogleWrapper{
    margin-top: 15px;
}

.google-btn {
  position: relative;    
  width: 200px;
  height: 42px;
  background-color: $google-blue;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
  transition: .2s;
  cursor: pointer;
    .google-icon-wrapper {
      position: absolute;
      cursor: pointer;
      
      margin-top: 1px;
      margin-left: 1px;
      width: 40px;
      height: 40px;
      border-radius: 2px;
      background-color: $white;
      transition: .2s;
    }
    .google-icon {
      position: absolute;
      margin-top: 11px;
      margin-left: 11px;
      width: 18px;
      height: 18px;
      cursor: pointer;
    }
    .btn-text {
      position: absolute;
      right: 17px;
      top: 2px;
      color: $white;
      font-size: 14px;
      letter-spacing: 0.5px;
      font-family: "Roboto";
      font-weight: 500;
      pointer-events: none;
      cursor: pointer;
    }
  &:hover {
    box-shadow: 0 0 6px $google-blue;
    
  }
  &:active {
    .google-icon-wrapper{
        background: hsl(0, 0%, 90%);
    }
    background: $button-active-blue;
  }
}

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');

